// section content ---------------
section#career-path-vut {
	// padding: 0 0px 30px;
	// background: #fff;
	// text-align: center;
	// max-width: 100%;

	.section-container {
		background-color: white;

		.list-container-action-button {
			padding-left: 0.5rem;
			padding-right: 0.5rem;
		}

		.text-content {
			max-width: 1500px;
			margin: auto;
			// height: 100%;
			padding: 40px 5rem;
			text-align: center;

			.pageTitle {
				margin-bottom: 60px;
				text-align: center;
			}

			.add-skill-modal .ant-modal-header {
				background-color: #32c4ff8f;
			}

			.edit-skill-modal .ant-modal-header {
				background-color: gold;
			}

			.list-container {
				display: flex;
				justify-content: center;
				flex-wrap: wrap;
				//margin-top: 50px;
			}

			.list-item {
				width: 300px;
				// height: 100px;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;
				border: 1px solid lightgrey;
				border-radius: 5px;
				padding: 8px;
				margin-bottom: 8px;
				transition: background-color 0.2s ease;
				background-color: #f9f9f9;
				box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
				0px 1px 1px 0px rgba(0, 0, 0, 0.14),
				0px 1px 3px 0px rgba(0, 0, 0, 0.12);
			}

			.list-item:hover {
				background-color: #f9f9f9;
			}

			.list-item.disabled {
				background-color: #f9f9f9;
			}
		}
	}

	.skill-list {
		width: 100%;
		text-align: left;

		.skill-list-item {
			margin-bottom: 2rem;
			background-color: rgba(222, 241, 254, 0.4);

			&.disabled {
				background-color: #f2f2f2;
			}

		}

		.skill-list-item-actions {

			display: flex;
			align-items: center;

			.skill-list-item-action-edit {
				margin-right: 0.5rem;
				margin-left: 0.25rem;
			}

			.skill-list-item-action-delete:hover {
				color: var(--danger);
				border-color: var(--danger);
			}
		}

		.skill-list-item-title-container {
			display: flex;
			flex-flow: row nowrap;
			align-items: center;

			.anticon {
				margin-left: 1rem;
				//margin-right: 0.5rem;
			}
		}

		.skill-list-item-title {
			margin: 0 0.5rem 0 1rem;
			white-space: wrap;

		}
	}

	.left-col {
		padding-right: 1rem;

		.list-container-title {
			text-align: center;
			//display: flex;
			//flex-flow: row nowrap;
			//justify-content: space-between;
			//align-items: center;
		}

		.list-container-actions {
			display: flex;
			flex-flow: row nowrap;
			justify-content: space-between;
			align-items: center;
			padding-bottom: 1rem;

			.list-container-import-action {
				margin-right: 0.5rem;
			}
		}

	}

	.right-col {
		padding-left: 1rem;

		.statistics-title {
			text-align: center;
			//display: flex;
			//flex-flow: row nowrap;
			//justify-content: space-between;
			//align-items: center;
		}

		.statistics {
			.ant-table-thead {
				.ant-table-cell {
					//font-weight: 700;
					padding: 8px;
				}
			}

			.ant-table-tbody {
				.ant-table-cell {
					padding: 2px 8px;
				}
			}

			.ant-table-summary {
				.ant-table-cell {
					//font-weight: 700;
					padding: 8px;
				}
			}

			//.highSkillLevel {
			//	color: #90EE90;
			//}
		}
	}

	.skills-popover {
		.skills-popover-title {
			font-weight: 700;
			margin-bottom: 1rem;
		}

		.skills-popover-subtitle {
			//color: #000;
			font-weight: 600;
		}
	}
}

// modals ---------------

.add-skill-modal,
.edit-skill-modal {
	.analyze-button-row {
		margin-bottom: 24px;

		.ant-col:first-child {
			padding-right: 10px;
			display: inline-flex;
			align-items: center;
			justify-content: right;
		}
	}

	.loading-icon-container {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 190px;
		flex-direction: column;

		// antd fix
		.ant-spin-text {
			padding-top: 5px;
			font-size: 14px;
		}
	}
}

.trainings-browser-modal {
	.trainings-browser {
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
		//overflow-x: auto;
		//height: 500px;
		//overflow: scroll;
		max-height: 70vh;

		.trainings-browser-filter {
			width: 40%;
			//overflow-y: auto;
			.trainings-browser-filter-title {
				text-align: center;
			}

			.ant-form-item-control {
				max-width: unset;
			}

			.content-scroll {
				padding-right: 12px;
			}
		}

		.trainings-browser-list {
			width: 60%;
			overflow: auto;
			margin-left: 10px;

			.trainings-list {
				padding-left: 1rem;

				.trainings-list-item-title-container {
					display: flex;
					flex-flow: row nowrap;
					align-items: center;

					.trainings-list-item-checkbox {
						margin-right: 1rem;
						margin-bottom: 0.5rem;
					}
				}
			}

			.ant-card {
				margin-bottom: 1rem;
				background-color: #f6f6f6;
				border-radius: 10px;
				padding: 5px;


				.other-skill-group {
					color: rgba(0, 0, 0, 0.45)
				}
			}

			.ant-card-head-wrapper {
				align-items: flex-start;

				.ant-card-head-title {
					white-space: wrap;
				}
			}

		}
	}
}

.training-info-icon {
	color: #1890ff;
}

.training-additional-info {
	width: 36rem;

	.training-description {
		margin-bottom: 0.5rem;
	}

	.training-data-link {
		margin-top: 0.5rem;
	}

	.training-data-content {
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		justify-content: space-between;

		.ant-table {
			width: 100%;

			.ant-table-tbody {
				.ant-table-cell {
					padding: 2px 8px;
				}
			}
		}
	}
}

.skill-analysis-info-icon {
	color: #1890ff;
}

.skill-analysis-info {
	max-width: 36rem;
	max-height: 40vh;
	overflow-y: auto;
}
