@import '~antd/dist/antd.css';

body.ant-scrolling-effect {
  width: 100% !important;
}

body {
  min-width: 1345px;
  // min-height: 880px;
  background-color: #f0f2f5 !important;
}

#root {
  height: 100%;
}

.layout {
  min-height: 100%;
  display: flex;
  flex-flow: column;

  header {
    // background-image: url("../../assets/banner.jpg");
    // background-color: black;
    // background-position: center;
    // background-repeat: no-repeat;
    // background-size: auto 64px;

    .header-container {
      max-width: 1920px;
      margin: auto;

      .logo {
        float: left;

        img {
          width: 120px;
        }
      }

      .ant-menu {
        margin-left: 50px;
        float: left;
        background-color: transparent;

        .red {
          background-color: #c91e1e;
        }
      }

      .loginIcon {
        float: right;
        //color: white;

        .ant-avatar {
          margin-bottom: 3px;
          cursor: pointer;
          margin-left: 1.5em;
          margin-right: 1.5em;
        }

        .userIcon.logged {
          background-color: #3e9dd1;
        }

        .userIcon:hover {
          box-shadow: 0px 0px 2px 2px rgba(255,255,255,0.91);
        }

        .ant-btn {
          // margin-left: 1.2em;
        }

        .ant-btn:hover {
          color: rgb(64, 169, 255);
          border-color: rgb(64, 169, 255);
        }
      }
    }
  }

  main.site-layout-content  {

    section {
      max-width: 1920px;
      width: 100%;
      // min-height: calc(100vh - 64px - 46px - 30px);
      min-height: calc(100vh - 64px - 46px - 0px);
      margin: 0 auto;
    }

    .vertCenter {
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 60vh;
      flex-direction: column;

      // antd fix
      .ant-spin-text {
        padding-top: 5px;
        font-size: 14px;
      }
    }

    .pageTitle {
      text-align: center;
      margin-bottom: 1.2em;
      margin-top: 0.8em;
    }
  }

  footer {
    padding: 12px 50px;
  }
}

// modals -----------

.user-edit-modal .ant-modal-header,
#user-profile .ant-popover-title {
  background-color: #46b6f3;
}

.forgot-modal .ant-modal-header,
.register-modal .ant-modal-header,
.feedback-modal .ant-modal-header,
.login-modal .ant-modal-header {
  background-color: #fbfbfb;
}

.feedback-modal .ant-modal-body {
  padding-bottom: 40px;
  text-align: center;

  h4 {
    padding-bottom: 12px;
  }

  button.selected-answer {
    color: #40a9ff;
    border-color: #40a9ff;
  } 

  .pseudo-button {
    display: inline-block;
    padding: 4px 16px;
    margin: 0 5px;
    border: 1px solid #d9d9d9;
    border-radius: 32px;
    cursor: pointer;
    transition: 
      color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), 
      border-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  }

  .pseudo-button.selected, .pseudo-button:hover {
    color:#40a9ff;
    border-color: #40a9ff;
  }

  .pseudo-button.selected {
    font-weight: 500;
  }

  .send-button {
    margin-top: 20px;
  }

}

.ant-modal-footer {
  background-color: #fbfbfb;
}

.loginForm{
  max-width: 88%;
  margin: auto;
  margin-top: 10px;

  .button{
    width: 100%;
  }
}

button.buttonLink {
  padding-left: 0px;
}


// popovers

.profileInfo {
  max-width: 500px;
  min-width: 170px;

  table {
    margin: 5px;
  }

  td:first-child {
    font-weight: bold;
    padding-right: 20px;
  }

  div {
    margin: 10px;
    text-align: center;
  }
}


// the rest ------------


//antd fix
.icon-position-fix svg,
.ant-message svg {
  margin-top: -4px;
}

// scroll
.content-scroll {
  height: 100%;
  padding-right: 7px;
  padding-bottom: 7px;
  overflow-x: hidden;
  overflow-y: auto;
}

.content-scroll::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 2px rgba(0,0,0,0.9);
  border-radius: 5px;
  background-color: #F5F5F5;
}

.content-scroll::-webkit-scrollbar {
  width: 5px;
}

.content-scroll::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,.3);
  background-color: #999;
}
